.view-button {
  display: inline-block;
  margin: 0 0.5rem;
  padding: 0.3rem 1rem;
  font-size: 18px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: black;
  background-color: white;
  border: 1.3px solid silver;
  border-bottom: 0;
  border-radius: 5px;
